@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css);
*{box-sizing:border-box;padding:0;margin:0}html{height:-webkit-fill-available}body{width:100vw;min-height:100vh;min-height:-webkit-fill-available;padding:20px;background-color:#f5f5f5;font-family:"Nunito",sans-serif}button{cursor:pointer;-webkit-tap-highlight-color:transparent}.app{position:relative;z-index:1;width:400px;margin:auto;margin-top:5vh;padding:15px;border-radius:8px;background-color:#fcfcfc;box-shadow:1px 1px 10px 1px rgba(0,0,0,.1)}@media screen and (max-width: 430px){.app{width:100%}}
.header{display:flex;justify-content:space-between;align-items:center;padding-bottom:15px;color:#05386b}.toggle-deletion{width:30px;height:30px;border-style:none;background-color:transparent;background-image:url(/todo-app/static/media/pencil.7f467b3f.svg);background-position:center;background-size:22px 22px;background-repeat:no-repeat;opacity:.8}
.current-date{font-size:1.5rem;font-weight:400}@media screen and (max-width: 380px){.current-date{font-size:1.2rem}}@media screen and (max-width: 330px){.current-date{font-size:1.1rem}}
.datepicker-toggle{position:relative;width:35px;height:30px;border-style:none;background-color:transparent;background-image:url(/todo-app/static/media/calendar.8c22a4ed.svg);background-position:center;background-size:22px 22px;background-repeat:no-repeat;opacity:.8}.datepicker-container{position:absolute;z-index:10;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.overlay{position:fixed;z-index:9;top:0;right:0;bottom:0;left:0;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);background-color:rgba(0,0,0,.15)}.react-datepicker{font-size:1.1rem;font-family:inherit;border-style:none;box-shadow:1px 1px 8px 2px rgba(0,0,0,.1)}.react-datepicker__header{border-bottom-style:none;background-color:#5cdb95}.react-datepicker__current-month{font-size:1.3rem;color:#fcfcfc;font-weight:400}.react-datepicker__day-name{color:#fcfcfc;font-weight:400;width:2.1rem;line-height:2.1rem;margin:.2rem}.react-datepicker__day{width:2.1rem;line-height:2.1rem;margin:.2rem}.react-datepicker__navigation{top:4px;-webkit-tap-highlight-color:transparent}.react-datepicker__navigation-icon::before,.react-datepicker__navigation:hover *::before{border-color:#fcfcfc}.react-datepicker__day--keyboard-selected,.react-datepicker__day--selected{background-color:#5cdb95}.react-datepicker__day--keyboard-selected:hover,.react-datepicker__day--selected:hover{background-color:#32d27a}.react-datepicker__day--selected{background-color:#5cdb95}
.task-count{margin-left:auto;margin-right:5px;opacity:.5;font-size:.9rem;font-weight:400;text-transform:uppercase;white-space:nowrap}@media screen and (max-width: 380px){.task-count{font-size:.8rem}}@media screen and (max-width: 330px){.task-count{font-size:.7rem;margin-right:0}}
.task-form{display:flex;justify-content:flex-end;align-items:center}.task-form__input{width:70%;height:25px;padding:5px;padding-top:0;font-size:1rem;color:#5cdb95;background-color:transparent;border-style:none;border-radius:0;border-bottom:2px solid #5cdb95;-webkit-tap-highlight-color:transparent;caret-color:#5cdb95;outline-color:#fcfcfc;outline-style:solid}.task-form__submit{width:50px;height:50px;margin-left:20px;border-radius:50%;border-style:none;color:#fff;font-family:inherit;font-size:2rem;font-weight:300;background-color:#5cdb95}
.task-wrapper{-ms-overflow-style:none;scrollbar-width:none}.task-wrapper::-webkit-scrollbar{display:none}.task-wrapper{max-height:55vh;padding:25px 10px 0px 10px;overflow-y:auto;list-style-type:none}.task-wrapper__item{position:relative;z-index:1;display:flex;justify-content:space-between;align-items:center;min-height:36px;padding:6px;font-size:1.1rem;font-weight:400;-webkit-tap-highlight-color:transparent}.task-wrapper__check{position:absolute;z-index:1;opacity:0}.task-wrapper__check:checked+label{color:#c2c2c2;font-weight:300;-webkit-text-decoration-line:line-through;text-decoration-line:line-through;-webkit-text-decoration-style:solid;text-decoration-style:solid;text-decoration-thickness:1px}.task-wrapper__check:checked+label:before{border-color:#5c5c5c;color:#5cdb95;opacity:.6}.task-wrapper__check:focus+label::before{box-shadow:0px 0px 2px 2px rgba(5,56,107,.08)}.task-wrapper__value{display:flex;align-items:center;padding-left:22px;color:#05386b}.task-wrapper__value:before{content:"";position:absolute;left:0;display:flex;justify-content:center;align-items:center;flex-shrink:0;width:1rem;height:1rem;border:1px solid #05386b;border-radius:2px;font-size:1.4rem;font-weight:600;font-family:"Font Awesome 5 Free";color:transparent;transition-property:color,border-color;transition-duration:150ms;transition-timing-function:ease-in}.task-wrapper__delete{flex-shrink:0;width:1.2rem;height:1.2rem;margin:3px;border-style:none;background-color:transparent;background-image:url(/todo-app/static/media/delete.d15b0876.svg);background-position:center;background-size:1.2rem 1.2rem;opacity:0;pointer-events:none;transition:opacity 100ms ease-in}.task-wrapper_editing .task-wrapper__delete{opacity:.7;pointer-events:visible}.task-wrapper_editing .task-wrapper__item{-webkit-animation:taskEdit 200ms ease-in;animation:taskEdit 200ms ease-in}@-webkit-keyframes taskEdit{50%{-webkit-transform:translateX(-10px);transform:translateX(-10px)}100%{-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes taskEdit{50%{-webkit-transform:translateX(-10px);transform:translateX(-10px)}100%{-webkit-transform:translateX(0px);transform:translateX(0px)}}
.footer{position:absolute;bottom:20px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center;font-size:.8rem;font-weight:300;color:#5cdb95}.footer__link{text-decoration:none;color:#05386b}
