.task-wrapper{-ms-overflow-style:none;scrollbar-width:none}.task-wrapper::-webkit-scrollbar{display:none}.task-wrapper{max-height:55vh;padding:25px 10px 0px 10px;overflow-y:auto;list-style-type:none}.task-wrapper__item{position:relative;z-index:1;display:flex;justify-content:space-between;align-items:center;min-height:36px;padding:6px;font-size:1.1rem;font-weight:400;-webkit-tap-highlight-color:transparent}.task-wrapper__check{position:absolute;z-index:1;opacity:0}.task-wrapper__check:checked+label{color:#c2c2c2;font-weight:300;text-decoration-line:line-through;text-decoration-style:solid;text-decoration-thickness:1px}.task-wrapper__check:checked+label:before{border-color:#5c5c5c;color:#5cdb95;opacity:.6}.task-wrapper__check:focus+label::before{box-shadow:0px 0px 2px 2px rgba(5,56,107,.08)}.task-wrapper__value{display:flex;align-items:center;padding-left:22px;color:#05386b}.task-wrapper__value:before{content:"";position:absolute;left:0;display:flex;justify-content:center;align-items:center;flex-shrink:0;width:1rem;height:1rem;border:1px solid #05386b;border-radius:2px;font-size:1.4rem;font-weight:600;font-family:"Font Awesome 5 Free";color:transparent;transition-property:color,border-color;transition-duration:150ms;transition-timing-function:ease-in}.task-wrapper__delete{flex-shrink:0;width:1.2rem;height:1.2rem;margin:3px;border-style:none;background-color:transparent;background-image:url("../../assets/icons/delete.svg");background-position:center;background-size:1.2rem 1.2rem;opacity:0;pointer-events:none;transition:opacity 100ms ease-in}.task-wrapper_editing .task-wrapper__delete{opacity:.7;pointer-events:visible}.task-wrapper_editing .task-wrapper__item{animation:taskEdit 200ms ease-in}@keyframes taskEdit{50%{transform:translateX(-10px)}100%{transform:translateX(0px)}}